const Card = ({ windowWidth, text, image, title, icon }) => {
  if (windowWidth < 1200) {
    return (
      <>
        <div className='col-12 col-md-7 my-5'>
          <CardWrapperSm>
            <div className='pt-3 text-center'>
              <div className='col-12 d-flex justify-content-center align-items-center'>
                <img src={icon} className='img-fluid' alt='Икона на интерактивна картичка' />
              </div>
              <div className='col-12 d-flex justify-content-center align-items-center'>
                {title}
              </div>
              <div className='col-12 mt-1 d-flex justify-content-center align-items-center'>
                <p>{text}</p>
              </div>
            </div>
          </CardWrapperSm>
        </div>
      </>
    )
  } else {
    return (
      <>
        <div className='row d-flex justify-content-end'>
          <div className='col-6'>
            <CardWrapper className='horizontal-steps-card'>
              <CardContainer className='text-left'>
                <div className='row'>
                  <div className='col-4'>
                    <img src={icon} className='img-fluid' alt='Икона на интерактивна картичка' />
                  </div>
                  <div className='col-8 d-flex justify-content-start align-items-center'>
                    {title}
                  </div>
                </div>
                <div className='col-12 mt-2 px-3'>
                  <p>{text}</p>
                </div>
              </CardContainer>
            </CardWrapper>
          </div>
          <div className='col-xl-3 d-none d-xl-block'>
            <img src={image} className='img-fluids card-image' alt='Снимка на интерактивна картичка' />
          </div>
        </div>
      </>
    )
  }
}

const CardWrapperSm = styled.div`
  background: #FFFFFF;
  box-shadow: 0px 0px 102px rgba(17, 41, 70, 0.25);
  border-radius: 16px;
`;

const CardWrapper = styled.div`
  background-image: url(${cardBg});
  background-repeat: no-repeat;
  background-position: top;
  background-size: cover;
  height: 538px;
  width: auto;
`;

const CardContainer = styled.div`
  padding-top: 110px;
`;

const settings = {
  arrows: true,
  speed: 2000,
  infinite: false,
  centerMode: false,
  autoplay: false,
  autoplaySpeed: 5000,
  slidesToShow: 1,
  slidesToScroll: 1,
  focusOnSelect: true,
  pauseOnFocus: true,
  responsive: [
    {
      breakpoint: 700,
      settings: {
        arrows: false,
        dots: true,
      }
    },
  ]
}

export default HorizontalSteps;