import React, { useState } from 'react';
import styled from 'styled-components';
import reasonsData from '../../constants/index';
import Slider from "react-slick";
import SectionTitle from './section-titles';

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const Card = styled.div`
  width: 275px;
  height: 470px;
  margin-left: 5px;
  margin-right: 5px;
  background: #FFFFFF;
  box-shadow: 0px 0px 30px rgba(51, 1, 109, 0.1);
  border-radius: 8px;
  border-bottom: 10px solid ${props => props.color};
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: center;
  background-color: ${props => props.bg};
  transition: 0.2s;
`

const Reasons = () => {

  return (
    <>
      <div className='row mb-5'>
        <SectionTitle>
          <h2 className='mb-5'><span className='text-purple'>Четири причини</span> да се качиш на NetSurf-а</h2>
          <hr className='center' />
        </SectionTitle>
        <div className='row w-100 d-none d-xl-flex justify-content-center align-items center'>
          {reasonsData.reasons.map((r, index) => (
            <div key={index}>
              <CardContent
                title={r.title}
                content={r.content}
                image={r.image}
                color={r.color}
              />
            </div>
          ))}
        </div>

        <div className='col-12 px-md-5 d-block d-xl-none reasons-slider'>
          <Slider {...settings}>
            {reasonsData.reasons.map((r, index) => (
              <div key={index}>
                <CardContent
                  title={r.title}
                  content={r.content}
                  image={r.image}
                  color={r.color}
                />
              </div>
            ))}
          </Slider>
        </div>
      </div>
    </>
  );
}

const CardContent = ({ title, image, content, color }) => {
  const [isOpen, setOpen] = useState(false);

  if (isOpen) {
    return (
      <Card className='px-3 pt-3 text-left card-open' bg={color} color={color} onMouseLeave={(_) => setOpen(false)} onClick={(_) => setOpen(!isOpen)}>
        <p>{content}</p>
        <div className='text-center'>
          <span className='icon reason-close'></span>
        </div>
      </Card>
    )
  } else {
    return (
      <Card className='px-2 py-3 text-center' bg={"white"} color={color} onMouseEnter={(_) => setOpen(true)} onClick={(_) => setOpen(!isOpen)}>
        <h3>{title}</h3>
        <img src={image} alt='Причина 1' />
        <div>
          <span className='icon reason-open'></span>
        </div>
      </Card>
    );
  }

}

const settings = {
  dots: false,
  speed: 2000,
  centerMode: true,
  autoplay: true,
  autoplaySpeed: 5000,
  slidesToShow: 4,
  slidesToScroll: 2,
  focusOnSelect: true,
  pauseOnFocus: true,
  responsive: [
    {
      breakpoint: 1200,
      settings: {
        slidesToShow: 3,
        centerMode: true,
        dots: true,
        centerPadding: '0px',
      }
    },
    {
      breakpoint: 930,
      settings: {
        slidesToShow: 2,
        centerMode: true,
        dots: true,
      }
    },
    {
      breakpoint: 690,
      settings: {
        slidesToShow: 2,
        slidesToScroll: 2,
        centerMode: true,
        arrows: false,
        dots: true,
      }
    },
    {
      breakpoint: 500,
      settings: {
        slidesToShow: 1,
        centerMode: true,
        centerPadding: '50px',
        arrows: false,
        dots: true,
      }
    },
    {
      breakpoint: 400,
      settings: {
        slidesToShow: 1,
        centerMode: true,
        centerPadding: '25px',
        arrows: false,
        dots: true,
      }
    },
    {
      breakpoint: 370,
      settings: {
        slidesToShow: 1,
        centerMode: true,
        centerPadding: '0px',
        arrows: false,
        dots: true,
      }
    },
  ],
}

export default Reasons;