import React from 'react';
import Slider from 'react-slick';
import SectionTitle from './section-titles';
import styled from 'styled-components';

import testimonialBg from '../../images/index/testimonial-bg.svg';
import arrowRight from '../../images/index/right-arrow.svg'
import arrowLeft from '../../images/index/left-arrow.svg'
import constants from '../../constants/index';

import nonActiveItem from '../../images/icons/non-active-list-item.svg';
import activeItem from '../../images/icons/active-list-item.svg';

const TestimonialWrapper = styled.div`
  background-image: url(${(props) => props.testimonialBg});
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  min-height: 540px;
  width: 540px;
  text-align: center;
`;

const TestimonialTextBox = styled.div`
  width: 540px;
`
const TestimonialSlider = () => {

  return (
    <>
      <div className='row d-flex justify-content-center'>
        <div className='col-12'>
          <SectionTitle>
            <h2>Какво мислят<br /><span className='text-purple'>другите за нас</span></h2>
            <hr className='center' />
          </SectionTitle>
        </div>

        <div className='col-12 col-md-8 px-md-4 reasons-slider'>
          <Slider {...settings}>
            {constants.clientOpinions.map(({ name, opinion, date }, index) => (
              <TestimonialWrapper key={index} testimonialBg={testimonialBg} className='col-12 d-flex justify-content-center align-items-center'>
                <TestimonialTextBox className='px-4 testimonial pt-5'>
                  <h3>{name}</h3>
                  <p className='text-testimonial'>{opinion}</p>
                  <h5 className='d-none d-sm-inline-block w-100'>{date}</h5>
                  <BulletPoints
                    count={constants.clientOpinions.length}
                    active={index}
                  />
                </TestimonialTextBox>
              </TestimonialWrapper>
            ))}
          </Slider>
        </div>
      </div>
    </>
  )
}

const BulletPoints = ({ count, active }) => {
  let res = [];
  for (var i = 0; i < count; i++) {
    if (i === active) {
      res.push(<li className='d-inline-block mx-1'><img src={activeItem} alt='активен елемент' /></li>)
    } else {
      res.push(<li className='d-inline-block mx-1'><img src={nonActiveItem} alt='неактивен елемент' /></li>)
    }
  }

  return (
    <div className='d-none d-md-inline-block w-100 pr-5 mt-2'>
      <ul className='inline-list'>
        {res.map((element, index) => {
          return element;
        })}
      </ul>
    </div>
  )
}

const NextArrow = ({ className, style, onClick }) => {
  return (
    <div
      className={className}
      style={{ ...style, display: 'block' }}
      onClick={onClick}
    >
      <img src={arrowRight} className='img-fluid' alt="Arrow Right" />
    </div>
  );
}
const PrevArrow = ({ className, style, onClick }) => {
  return (
    <div
      className={className}
      style={{ ...style, display: 'block' }}
      onClick={onClick}
    >
      <img src={arrowLeft} className='img-fluid' alt="Arrow Left" />
    </div>
  );
}

const settings = {
  arrows: true,
  speed: 2000,
  infinite: false,
  centerMode: false,
  autoplay: false,
  autoplaySpeed: 5000,
  slidesToShow: 1,
  slidesToScroll: 1,
  focusOnSelect: true,
  pauseOnFocus: true,
  nextArrow: <NextArrow />,
  prevArrow: <PrevArrow />,
  responsive: [
    {
      breakpoint: 700,
      settings: {
        arrows: false,
        dots: true,
      }
    },
  ]
}

export default TestimonialSlider;