import React from 'react';
import { Link } from 'gatsby';
import Img from "gatsby-image";

const ImageTextSection = ({ imgSrc, imgAlt, sectionTitle, undertext, list, linkTo, linkText, order }) => {

  return (
    <>
      <div className='row d-flex my-5 mx-md-5 px-lg-5'>
        {!order && (
          <div className='text-center d-block d-lg-none text-center w-100'>
            {sectionTitle}
            <hr className='my-4 d-none d-lg-block' />
            <hr className='my-4 center d-block d-lg-none' />
          </div>
        )}

        <div className={'col-12 col-lg-6 ' + (order && 'order-lg-1')}>
          <div style={{ maxHeight: "750px" }}>
            <Img
              fluid={imgSrc}
              style={{ maxHeight: "750px" }}
              imgStyle={{ objectFit: "contain" }}
              alt={imgAlt}
            />
          </div>
        </div>
        <div className={'col-12 col-lg-6 px-xl-5 text-center text-lg-left ' + (order && 'order-lg-0 d-flex justify-content-center')}>
          <div className='col-12 col-xl-8'>
            {order && (
              <div className='text-center d-block d-lg-none'>
                {sectionTitle}
                <hr className='my-4 d-none d-lg-block' />
                <hr className='my-4 center d-block d-lg-none' />
              </div>
            )}
            <div className='text-lg-left d-none d-lg-block'>
              {sectionTitle}
              <hr className='my-4 d-none d-lg-block' />
              <hr className='my-4 d-block d-lg-none' />
            </div>
            <p>{undertext}</p>
            <p className='mb-5'>
              <ul className='blue-ticks'>
                {list.map((item, index) => (
                  <li key={index}>{item}</li>
                ))}
              </ul>
            </p>
            <Link to={linkTo} className='btn btn-primary green w-100 px-5'>{linkText}</Link>
          </div>
        </div>
      </div>
    </>
  )
}

export default ImageTextSection;